import React, { Component, Suspense } from 'react';
import DisplayCode from '../../../PublishCommonComponents/DisplayCode';
class AndroidTvCode extends Component {

    render() {
        const { data } = this.props
        return <div className="card text-decoration-none">
            <div className="card-body p-lg-7">
                <div className="m-auto">
                    <div className="embed_code">
                        <h5 className="mb-5 fw-bolder">Steps To Display Social wall With Android TV App</h5>
                        <h6>Step 1 : Collect The Generated Social Wall Display Code</h6>
                        <DisplayCode displayCode={data && Object.keys(data).length > 0 ? data.wallId : null} />
                        <h6 className="mb-5">Step 2 : Display Social wall with Android TV</h6>
                        <ul className="list-unstyled list-points fs-7">
                            <li className="mb-3">Go to your play store on your tv and search Taggbox Display</li>
                            <li className="mb-3">Install Taggbox Display App and Lunch</li>
                            <li className="mb-3">Enter Social wall Display code in App</li>
                            <li className="mb-3">Done! Youâ€™re all set to showcase your social wall.</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default AndroidTvCode;